.header {
  color: white;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(40%, #690069), to(#b701b7));
  background-image: -webkit-linear-gradient(top, #690069 40%, #b701b7 100%);
  background-image: -o-linear-gradient(top, #690069 40%, #b701b7 100%);
  background-image: linear-gradient(to bottom, #690069 40%, #b701b7 100%);
  text-align: center;
}

h1 {
  font-size: 2em;
  padding: .5em 0;
  font-weight: 700;
  cursor: default;
}


